import React,{Component , Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import  compose  from 'recompose/compose';
import { AppBar, Toolbar,IconButton,Typography,Hidden,Drawer,List,ListItem,ListItemText,Collapse} from '@material-ui/core';
import {Menu} from '@material-ui/icons';
// import InboxIcon from '@material-ui/icons/MoveToInbox';
// import DraftsIcon from '@material-ui/icons/Drafts';
// import SendIcon from '@material-ui/icons/Send';
import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';
// import StarBorder from '@material-ui/icons/StarBorder'
import {  withStyles  } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
// import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import '../../css/mdsp-css.min.css';

const drawerWidth = 240;

const styles = theme => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1
  },
  menuButton: {
    marginRight: theme.spacing(2),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
});

class Layout extends Component{
    state = {
        mobileOpen:false,
        listA:false
    }

    

    render(){
        const { classes, location:{pathname},children } = this.props;
        const { mobileOpen}= this.state;
        const { listA}=this.state;
        //const classes = useStyles();
        //const theme = useTheme();
        //const [mobileOpen, setMobileOpen] = React.useState(false);

        const handleDrawerToggle = () => {
            this.setState({mobileOpen: !this.state.mobileOpen})
        };

        const handleClick = () => {
          this.setState({listA: !this.state.listA})
        };

        const drawer = (
            <div className="mdsp mdsp-defaults">
              <Hidden smDown>
              <div className={classes.toolbar} />
              </Hidden>
                
              <List
                aria-labelledby="nested-list-subheader"
                className="list"
              >
                <ListItem button onClick={handleClick}>
                  <ListItemText primary={<b>Masters</b>} />
                  {listA ? <ExpandLess /> : <ExpandMore />}
                </ListItem>
                <Collapse in={listA} timeout="auto" unmountOnExit>
                  <List component="div" disablePadding>
                    <ListItem button className={classes.nested} component={Link} to ="/Home/MBasic" selected={'/Home/MBasic' === pathname}>
                      <ListItemText primary="Country" />
                    </ListItem>
                    <ListItem button className={classes.nested} component={Link} to ="/Home/MBasicMUI" selected={'/Home/MBasicMUI' === pathname}>
                      <ListItemText primary="Factory" />
                    </ListItem>
                  </List>
                </Collapse>
                <ListItem button component={Link} to ="/Home/writers" selected={'/Home/writers' === pathname}>
                  <ListItemText primary="Quality Management" />
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Production Management" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="EHS" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Raw Materials Management" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Finished Goods" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Checkshseet" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Maintenance Management" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Set Up" />                 
                </ListItem>
                <ListItem button >
                  <ListItemText primary="Reports" />                 
                </ListItem>
              </List>
            </div>
        );
        return <Fragment><div className={classes.root}>
        <CssBaseline />
        <AppBar position="fixed" className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              className={classes.menuButton}
            >
              <Menu />
            </IconButton>
            <Typography className="h1" noWrap>
              HALCYON - THINK RUBBER, THINK HALCYON
            </Typography>
          </Toolbar>
        </AppBar>
        <nav className={classes.drawer} aria-label="mailbox folders">
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation="css">
            <Drawer
              variant="temporary"
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
              ModalProps={{
                keepMounted: true, // Better open performance on mobile.
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant="permanent"
              open
            >
              {drawer}
            </Drawer>
          </Hidden>
        </nav>
        <main className={classes.content}>
          <div className={classes.toolbar} />
            {children}
        </main>
      </div>
      </Fragment>
    }
}

export default compose(
    withRouter,
    withStyles(styles)
)(Layout)

